import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkstate from 'linkstate';
import { route } from 'preact-router';

import mu from '../../lib/musher-util';

import style from './style.css';

const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {};

const RELOAD_INTERVAL_IN_SEC = 60;
const SCROLL_INTERVAL_IN_SEC = 5;
const CONTAINER_WIDTH = 450;
const CONTAINER_MAX_WIDTH = 1486;

function byBib(a, b) {
    const aHas = typeof a.musher !== 'undefined';
    const bHas = typeof b.musher !== 'undefined';
    return (bHas - aHas) || (aHas === true && a.musher.bib - b.musher.bib) || 0;
}

@observer
class OneTeam extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
            scrollPosition: 0,
        };
        this.updateTimer = {};
        this.scrollTimer = null;
        this.contestantContainer = null;
    }

    loadLiveRaceResults = async (props = this.props, teamIds, focusOnMushers) => {
        const { raceStore } = props.stores;
        const { race } = raceStore;
        await raceStore.getRaceTrackerData(race.raceTrackerRace);
        raceStore.getRaceTrackerActiveTeams(teamIds, focusOnMushers);
    }

    loadRaceResults = async (props = this.props, teamIds, focusOnMushers) => {
        const { raceStore } = props.stores;
        const { raceid } = props;

        await raceStore.loadWorkoutSummary(teamIds, raceid);
        raceStore.getWorkoutSummaryActiveTeams(teamIds, focusOnMushers);
    }

    loadRace = async (props = this.props) => {
        this.setState({ isLoading: true });
        const { raceStore, appState } = props.stores;
        const { raceid, classid } = props;
        await raceStore.load(raceid);
        const { race } = raceStore;
        const { focusOnMushers } = appState;

        const { selectedClassIds = [] } = appState;
        const selectedClassId = classid || selectedClassIds[0];

        if (classid) {
            appState.toggleSelectedClassId(classid, true);
        }

        const allTeamIds = race.classes
            .filter(currentClass => currentClass.id === selectedClassId)
            .filter(currentClass => currentClass.contestants && currentClass.contestants.length > 0)
            .map((currentClass) => {
                return currentClass.contestants.filter(musher => musher.team > 0).map(musher => musher.team);
            })
            .flat();
        if (allTeamIds.length > 0) {
            await raceStore.loadTeams(allTeamIds.join(','));
        }
        if (race.liveRace > 0) {
            await this.loadLiveRaceResults(this.props, allTeamIds, focusOnMushers);

        } else {
            await this.loadRaceResults(this.props, allTeamIds, focusOnMushers);
        }

        // const allTeamIds = race.classes.filter(currentClass => currentClass.contestants && currentClass.contestants.length > 0).map((currentClass) => {
        //     return currentClass.contestants.filter(musher => musher.team > 0).map(musher => musher.team);
        // }).flat();

        // if (allTeamIds.length > 0) {
        //     await raceStore.loadTeams(allTeamIds.join(','));
        //     if (race.liveRace > 0) {
        //         await this.loadLiveRaceResults(props, allTeamIds, focusOnMushers);

        //     } else {
        //         await this.loadRaceResults(props, allTeamIds, focusOnMushers);
        //     }
        // }
        raceStore.getAllClassesSortedBy();

        const currentClass = raceStore.getClassById(selectedClassId);
        if (currentClass && currentClass.raceTrackerResults) {
            raceStore.getRacetrackerResults(currentClass.raceTrackerResults);
        }

        // Set reload timer
        clearTimeout(this.updateTimer.reload);
        this.updateTimer.reload = setTimeout(async () => {
            const reloadLastEpoch = Math.floor(new Date().getTime() / 1000);
            this.setState({ reloadLastEpoch });
            this.loadRace(props);
        }, RELOAD_INTERVAL_IN_SEC * 1000);

        this.scrollList();
        this.setState({ isLoading: false });
    }

    scrollList = () => {
        this.scrollTimer = setTimeout(async () => {
            const { scrollPosition } = this.state;
            const maxScrollLeft = this.contestantContainer.scrollWidth - this.contestantContainer.clientWidth;
            // const containerMaxWidth = this.contestantContainer.offsetWidth;
            let newScrollPosition = scrollPosition + CONTAINER_WIDTH * 3;
            if (newScrollPosition > maxScrollLeft + CONTAINER_WIDTH * 3) {
                newScrollPosition = 0;
            } else {
                this.contestantContainer.scrollTo({
                    top: 0,
                    left: newScrollPosition,
                    behavior: 'smooth'
                });
                this.scrollList();
            }
            this.setState({
                scrollPosition: newScrollPosition,
            });
        }, SCROLL_INTERVAL_IN_SEC * 1000);
    }

    toggleFocusOnMusher = (e) => {
		const musherId = e.target.parentElement.dataset.musherid || e.target.dataset.musherid;
		const { appState, raceStore } = this.props.stores;
		appState.toggleFocusOnMusher(musherId);

		const { focusOnMushers } = appState;

        const { race } = raceStore;
        if (race.liveRace > 0) {
            raceStore.getRaceTrackerActiveTeams(null, focusOnMushers);
        } else {
            raceStore.getWorkoutSummaryActiveTeams(null, focusOnMushers);
        }
	}

	componentWillUnmount() {
		clearTimeout(this.updateTimer.reload);
		clearTimeout(this.updateTimer.tick);
	}

	componentDidMount() {
		this.loadRace();
	}

    componentWillReceiveProps(nextProps) {
        if (this.props.classid !== nextProps.classid) {
            this.loadRace(nextProps);
        }
	}

    render() {
		const { contentIsNotLoaded, lockMessage, scrollPosition } = this.state;
		const { userStore, raceStore, appState } = this.props.stores;
        const { classid, position = 'right', limit = 10 } = this.props;
		const { user = {}, teams = [], isAdmin } = userStore;
        const { language = 'en' } = user;

        const { darkmode, displayDetails, editUserDetails, selectedClassIds = [], focusOnMushers = [] } = appState;

		const { race = {}, teamList, classes = [] } = raceStore;
		const { activeTeams, waypoints, zoomToMarkers, raceTrackerResults } = raceStore;

		const lastAllowedChangeDate = new Date(race.endDate);
		lastAllowedChangeDate.setHours(lastAllowedChangeDate.getHours() + 2);

        const currentClass = race.classes ? race.classes.find(e => e.id === classid) || {} : {};

        let contestants = [];
        if (raceTrackerResults && raceTrackerResults.data) {
            contestants = raceTrackerResults.data.slice(0, limit).map((line) => {
                const lineData = raceStore.raceTrackerMapLine(line);
                const bib = parseInt(lineData.Bib, 10);
                let contestant = {};
                if (classes && classes[currentClass.id].contestants) {
                    contestant = classes[currentClass.id].contestants.find(e => e.musher.bib === bib) || {};
                }
                return contestant;
            });
        } else if (classes[currentClass.id] && classes[currentClass.id].contestants) {
            contestants = classes[currentClass.id].contestants.sort(byBib).slice(0, limit).map(contestant => contestant);
        }

        return (<>
            <div class={`fixed-top ${style.hoverElement}`} style='height: 100px;'>
                <div class={`${style.hoverelementonly}`}>
                    <div class={`row`}>
                        {race.classes && race.classes.sort((a, b) => a.distance - b.distance).map((currentClass, idx) => <>
                            <div class='col'>
                                <a
                                    class={`btn btn-block ${currentClass.id === classid ? 'btn-success' : 'btn-outline-success'} text-nowrap`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-id={currentClass.id}
                                    href={`/tv/top10ticker/${race.id}/${currentClass.id}`}
                                >
                                    {currentClass.title}
                                </a>
                            </div>
                        </>)}
                    </div>
                </div>
            </div>
			<div class='container-fluid position-relative' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP}; height: calc(100% - ${MARGIN_BOTTOM} - ${MARGIN_TOP});`}>
                <div
                    class='d-flex position-absolute'
                    style='
                        bottom: -185px;
                        left: 150px;
                        background-image: url("https://themusher.app/assets/tv-ticker.png");
                        background-position: 0 100%;
                        background-repeat: no-repeat;
                        width: 1780px;
                        height: 115px;
                    '
                >
                    <div
                        class='text-white text-left position-absolute'
                        style='
                            font-size: 2.3em;
                            font-weight: normal;
                            bottom: 50px;
                            left: 110px;
                            width: 550px;
                    '>
                        <span class='font-weight-light'>Topp 10</span> - GPS Ranking
                    </div>
                    <div
                        class='text-dark text-left position-absolute'
                        style={`
                            font-size: 2.0em;
                            font-weight: normal;
                            bottom: 4px;
                            left: 99px;
                            width: ${CONTAINER_MAX_WIDTH}px;
                    `}>
                        <div class='w-100'>
                            <div
                                class='d-flex flex-row flex-nowrap'
                                style='overflow: auto; scroll-snap-type: x mandatory;'
                                onScroll={this.scrollImages}
                                ref={c => this.contestantContainer = c}
                            >
                                {contestants && contestants.map((contestant, idx) => {
                                    const { musher = {}, currentTeam = {}, currentStages, currentResults = {}, completed, duration,
                                        normalizedDuration, weightedNormalizedDuration, totalKcalRelated, weightedTotalKcal,
                                        flatTotalKcal, percentHarder, loadIndex, raceDistance, isDone, normalizedKcal, normalizedFlatKcal,
                                        finishedDate, totalWorkoutTimeUsedSec,
                                    } = contestant;

                                    return <>
                                        <div class='clearfix position-relative p-0 mx-0'>
                                            <div
                                                class={`h-100 d-flex flex-nowrap justify-content-start align-items-center`}
                                                style={`
                                                    flex-wrap: wrap;
                                                    overflow-y: hidden;
                                                    width: ${CONTAINER_WIDTH}px;
                                                `}
                                                onTouchstart={(e) => { e.stopPropagation(); }}
                                                onTouchend={(e) => { e.stopPropagation(); }}
                                                onTouchmove={(e) => { e.stopPropagation(); }}
                                            >
                                                <div
                                                    class='pl-3 pr-2 clearfix text-white rounded-pill-right'
                                                    style={`width: 100px; background-color: ${mu.getRankingColor(idx + 1)}`}
                                                >
                                                    {musher.bib || idx + 1}
                                                </div>
                                                <div class='pl-1 pr-3'>
                                                    <nobr>
                                                        {musher.flag} {musher.firstname} {musher.lastname}<br />
                                                        {/* <small class='text-muted'>{musher.sportsClub}</small><br /> */}
                                                    </nobr>
                                                </div>
                                            </div>
                                        </div>
                                    </>;
                                })}
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </>);
    }
}

export default OneTeam;
